body {
  margin: 0%;
  padding: 0%;
  height: 100%;
  font-family: "Times New Roman", Times, serif;
  background-image: url(https://i.imgur.com/65spwyh.jpeg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 200%;
  display: flex;
  justify-content: center;
}

h1,
h3,
th {
  color: rgba(247, 221, 71, 0.808);
}

button {
  background-color: orange;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 2.5rem;
  padding: 8px;
  margin-top: 2%;
  width: 100%;
  height: 40px;
}
.Seeker {
  width: 100%;
  height: 40px;
  border-radius: 10px;
}
.Seeker2 {
  width: 103%;
  height: 45px;
  border-radius: 10px;
}
